<template>
  <div>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-text-field v-model="query">
        <template v-slot:label>Search For Fusarium Taxonomy</template>
      </v-text-field>

      <v-btn color="success" class="mr-4" @click="search">Search</v-btn>
      <v-btn color="error" class="mr-4" @click="reset">Clear</v-btn>
    </v-form>
    <p />
    <div v-if="searching">
      <i>Searching...</i>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { bus } from "../main.js";

export default {
  data: () => ({
    query: "",
    results: [],
    errors: [],
    selectedLandscapes: [],
    checkbox: false,
    searching: false
  }),
  methods: {
    reset() {
      this.$refs.form.reset();
    },

    search() {
      this.searching = true;
      axios
        .get(
          `https://www.ebi.ac.uk/proteins/api/taxonomy/name/${encodeURIComponent(
            this.query
          )}`
        )
        .then(response => {
          this.searching = false;
          this.results = response.data;
          bus.$emit("search_result", response.data)
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  created() {
    bus.$on("filterSelection", data => {
      this.selectedLandscapes = data;
    });
  }
};
</script>